import React from 'react';
import styled from 'styled-components';
import { Paginator, ArticlesPreviewWrapper } from '../atoms';
import { AuthorCard, ArticlePreviewCard } from '../molecules';
export const ArticlePreviewList = ({ postEdges, context, description, }) => {
    return (<Wrapper>
      <MainArea>
        {description}
        <ArticlesPreviewWrapper>
          {postEdges.map((edge) => (<ArticlePreviewCard key={edge.node.frontmatter.title} postInfo={edge}/>))}
        </ArticlesPreviewWrapper>
        <Paginator context={context}/>
      </MainArea>

      <AsideArea>
        <AsideItem>
          <AuthorCard />
        </AsideItem>
      </AsideArea>
    </Wrapper>);
};
